<template>
  <el-container>
    <el-header>
      <h2 class="text-center">XmBot</h2>
    </el-header>
    <el-main class="text-center">
      <el-row>{{homeTitle}}</el-row>
      <el-row>
        <el-button size="small" type="primary" v-if="!isLogin" @click="$router.push('/login')">登录</el-button>
        <el-button size="small" type="primary" v-else @click="logout">登出</el-button>
      </el-row>
      <el-row>
        <el-button size="small" type="success" @click="$router.push('/plugins-dict')">帮助手册</el-button>
      </el-row>
      <el-row v-if="isLogin">
        <el-button size="small" type="primary" @click="$router.push('/pcr/pool-setting')">PCR卡池设置</el-button>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
  export default {
    name: "Index",
    data() {
      return {
        user_id: ''
      }
    },
    computed: {
      isLogin() {
        return this.user_id && this.user_id !== ''
      },
      homeTitle() {
        if (this.isLogin) {
          return `欢迎:${this.user_id}!`
        } else {
          return '未登录'
        }
      }
    },
    methods: {
      logout() {
        this.$cookies.remove('token')
        this.$cookies.remove('user_id')
        this.$cookies.remove('salt')
        this.$cookies.remove('login_count')
        location.reload()
      }
    },
    created() {

    },
    mounted() {
      let user_id = this.$cookies.get('user_id')
      if (user_id) {
        this.user_id = user_id
      }
    }
  }
</script>

<style scoped>
  .el-row {
    margin-bottom: 10px;
  }
</style>



